<template>
<div id="app">
   <Search v-on:queryItems="queryItems" v-on:addItem="addItem" v-on:inputChange="inputChange"/>
  <div class="list">
      <div v-if="queryinfos.length" class="list">
        <Lazy v-for="data in queryinfos" :key="data.id">
          <div slot="item" class="item">      
              <Item :id="data.id" :url="data.url" :title="data.title" :desc="data.desc"
              v-on:delItem="delItem(data.id)"
              v-on:saveItem="saveItem()"/>
          </div>
          <div slot="skeleton">
            <Skeleton slot="skeleton"/>
          </div>
        </Lazy>
      </div>
      <div v-else>
          No Data
      </div>
  </div>
</div>
</template>

<script>
import urls from './util'
import Item from './item/Item'
import List from './list/list'
import Lazy from './list/Lazy'
import Skeleton from './item/skeleton'
import Search from './search/Search'
import info from './json'
import { mapState, mapMutations } from 'vuex'
import { setTimeout } from 'timers';
export default {
  name: 'app',
  components: {
    Item,
    List,
    Lazy,
    Skeleton,
    Search
  },
  data() {
    return {
      infos: info.data,
      urls: urls
    }
  },
  computed: {
    ...mapState(['list','searchText']), 
      queryinfos: function() {
        let that = this
        return this.list.filter(item => {  
          return item.title.toLowerCase().startsWith(this.searchText) || item.title.toLowerCase().includes(this.searchText)
        });
      }
  },
  mounted() {
    this.init(info.data)
  },
  methods: {
      ...mapMutations(['init','setSearchText','delItem']),
      queryItems(searchText) {
        console.log(Array.isArray(this.list))
        this.setSearchText(searchText)
      },
      delItem(id) {
         this.$confirm('你确定删除该项目吗？', {
                confirmButtonText: '确定',
                cancelButtonText: '取消'
            }).then(() => {
                // this.list.forEach((item,index) => {
                //     if(item.id == id) {
                //       this.list.splice(index,1)
                //     }
                //   })
                this.$store.commit('delItem',id)
                this.$message({
                    message: '删除成功',
                    type: 'success'
                })
            }).catch(() => {
                 this.$message({
                    message: '已取消删除',
                    type: 'info'
                })
            })
      },
      saveItem() {
        console.log('gogogogo')
      },
      addItem(formData) {
        if(formData.title&&formData.url&&formData.desc){
              this.$store.commit('addItem',formData)
              this.$message({
                message: '添加成功',
                type: 'success'
                })
        }else {
          this.$message({
            message: '请输入合法的内容',
            type: 'error'
          })
        }
        
      },
      inputChange(searchText) {
      }
  }
}
</script>

<style>
#app {
  width: 60%;
  margin: 0 auto;
  display: flex;
  display: -ms-flexbox;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
  flex-direction: column
}
.list {
  margin-top: 30px;
  display: flex;
  display: -webkit-flex;
  display: -ms-flexbox;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap
}
.item {
  align-self: flex-start
}
</style>



