<template>
    <div>
        <transition-group tag="div" name="fade" class="item" :class="{}" mode="out-in">
            <div v-if="isShow" key="url">
                <slot name="item"></slot>
            </div>
            <div v-else key="title" class="skeleton">
                <slot name="skeleton"></slot>
            </div>
        </transition-group>
    </div>
</template>

<script>
export default {
    props: {
        data: {
            type: Array,
            default: () => []
        }
    },
    data() {
        return {
            isShow: false,
            count: 0,
            io: null
        }
    },
    mounted() {
        this.io = new IntersectionObserver((entries) => {
            entries.forEach(entry => {
                if(entry.isIntersecting || entry.intersectionRatio) {
                    if(!this.isShow) {
                        this.isShow = true
                    }
                    this.io.unobserve(this.$el)
                }
            })
        })

        this.io.observe(this.$el)
    },
    beforeDestroy() {
        if(this.io) {
            this.io.disconnect()
        }
    }
}
</script>

<style scoped>
    .list {
        display: flex;
        display: -ms-flexbox;
        display: -webkit-flex;
        flex-direction: row
    }
    .skeleton {
        position: relative;
    }
    .fade-enter-active, .fade-leave-active {
        transition: opacity 0.2s;
    }
    .fade-enter-to, .fade-leave-to {
        opacity: 0;
    }
</style>





