<template>
    <div class="list" :class="{flexCol:isFlexCol,flexNoWrap:!isWrap}">
        <ul class="list-item" v-for="item in datas" id="slot">
            <slot name="item" :item="item" class="item"></slot>
        </ul>
    </div>
</template>

<script>
import Item from '../item/Item'
export default {

    name: 'ListLazy',

    props: {
        datas: {
            type: Array,
            default: ''
        },
        direction: {
            type: String,
            default: 'horizon'
        },
        wrap: {
            type: Boolean,
            default: true
        },
        threshold: {
            type: Array,
            default: () => [0]
        },
        viewport: {
            type: Object,
            default: null 
        },
        timeout: {
            type: Number,
            default: 1
        },
        maxtime: {
            type: Number,
            default: 50
        }
    },
    components: {
        Item
    },
    data() {
        return {
            isFlexCol: this.direction == 'vertical',
            isWrap: this.wrap == true,
            count: 0,
            isInit: false,
            timeoutID: null,
            io: null,
            loading: false,
            renderitems:[].concat(this.datas.slice(0,3)),
            see: true
        }
    },
    computed: {
        items: vm => {
            return vm.datas
        }
    },
    created() {
        if(this.timeout) {
            this.timeoutID = setTimeout(() => {
                this.init()
            },this.timeout)
        }
    },
    mounted() {
        let urls = [
            'https://fuguiguan.cn/images/road.jpg',
            'https://fuguiguan.cn/images/flower.jpg',
            'https://fuguiguan.cn/images/beauty.jpg',
            'https://fuguiguan.cn/images/cat.jpg',
            'https://fuguiguan.cn/images/cigarette.jpg',
            'https://fuguiguan.cn/images/city.jpg',
            'https://fuguiguan.cn/images/cloud.jpg',
            'https://fuguiguan.cn/images/dream.jpg',
            'https://fuguiguan.cn/images/girl.jpg',
            'https://fuguiguan.cn/images/heart.jpg',
            'https://fuguiguan.cn/images/kiss.jpg',
            'https://fuguiguan.cn/images/kobe.jpg',
            'https://fuguiguan.cn/images/marriage.jpg',
            'https://fuguiguan.cn/images/sky.jpg',
            'https://fuguiguan.cn/images/sun.jpg',
            'https://fuguiguan.cn/images/tree.jpg',
            'https://fuguiguan.cn/images/video.jpg',
            'https://fuguiguan.cn/images/watermelon.jpg',
            'https://fuguiguan.cn/images/flower.jpg',
            'https://fuguiguan.cn/images/woman.jpg',
            'https://fuguiguan.cn/images/woman.jpg'
        ]
        let index = 0
       let io = new IntersectionObserver(
        ([item]) => {
            // console.log(entries);
            // changes.forEach((change) => {  
            //     if(change.intersectionRatio>0) {
            //         this.see = false
            //         let img = change.target.children[0].children[0].children[0]
            //         img.setAttribute('src',urls[index])
            //         console.log(this.$el)
            //         index++
                    
            //         io.unobserve(change.target)
            //     }
            // })
            if(item.isIntersecting||item.intersectionRatio) {
                console.log(item.target)
                let img = item.target.children[0].children[0].children[0]
                    img.setAttribute('src',urls[index++])
                io.unobserve(item.target)
            }
            
        },{
            threshold: [0,0.25, 0.5, 0.75, 1]
        }
        )
        let eles = document.querySelectorAll('.item')
        Array.from(eles).forEach(ele => {
            io.observe(ele)
        })
    },
    // mounted() {
    //     if(!this.timeout) {
    //         let rootMargin
    //         switch (this.direction) {
    //             case 'vertical':
    //                 rootMargin = `${this.threshold} 0px`
    //                 break;
    //             case 'horizon':
    //                 rootMargin = `0px ${this.threshold}`
    //                 break;
            
    //             default:
    //                 break;
    //         }
    //         try {
    //             this.io = new IntersectionObserver(this.intersectionHandler, {
    //                 rootMargin,
    //                 root: this.viewport,
    //                 threshold: [0,0.25]
    //             });
    //             this.io.observe(this.$el)
    //         } catch (e) {
    //             this.init()
    //         }
    //     }
    // },
    beforeDestroy() {
        if(this.io) {
            this.io.unobserve(this.$el)
        }
    },
    methods: {
        intersectionHandler(entries) {
            if(entries[0].isIntersecting || entries[0].intersectionRatio) {
                this.init()
                this.io.unobserve(this.$el)
            }
        },
        init() {
            this.requestAnimationFrame(() => {
                this.init = true
                this.$emit('init')
            })
        },
        requestAnimationFrame (callback) {
            setTimeout(() => {
                if(this.isInit) return
                callback()
            },this.maxtime)
            return (window.requestAnimationFrame || ((callback) => setTimeout(callback, 1000 / 60)))(callback)
        }
    }
}
</script>

<style>
    .list {
        display: flex;
        display: -webkit-flex;
        flex-wrap: wrap;
    }
    .item {
        display: block;
        /* opacity: 0; */
    }
    .flexRow {
        flex-direction: row;
    }
    .flexCol {
        flex-direction: column;
    }
    .flexWrap {
        flex-wrap: wrap;
    }
    .flexNoWrap {
        flex-wrap: nowrap;
    }
    .itemRigth {
        /* margin-right: 'this.itemRigth'; */
    }
    .skeleton {
        height: 300px;
        width: 250px;
        border: 1px solid #eee;
        border-radius: 10px;
        overflow: hidden;
        margin-bottom: 25px;
        transition: box-shadow 0.8s,margin-top 0.5s;
        -webkit-transition:box-shadow 0.8s,margin-top 0.5s;
        -ms-transition: box-shadow 0.8s,margin-top 0.5s;
    }
    .skeleton:hover {
        box-shadow:0 3px 2px 3px #ccc;
	    margin-top: -10px;
    }
</style>




